import './style.css'
import * as THREE from 'three'
import * as dat from 'dat.gui'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'

import imagesLoaded from 'imagesloaded';
import FontFaceObserver from 'fontfaceobserver';

import gsap from 'gsap';
import Scroll from './scroll';

import fragment from './shaders/fragment.glsl'
import vertex from './shaders/vertex.glsl'
import noise from './shaders/noise.glsl'

import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js';
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js';
import { ShaderPass } from 'three/examples/jsm/postprocessing/ShaderPass.js';

/**
 * Base
 */

// Debug
const gui = new dat.GUI({ width: 340 })
const debugObject = {}

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()

/**
 * Preloading 
 */ 

// Fonts
// const fontOpen = new Promise(resolve => {
//     new FontFaceObserver("Open Sans").load().then(() => {
//       resolve();
//     });
//   });

//   const fontPlayfair = new Promise(resolve => {
//     new FontFaceObserver("Playfair Display").load().then(() => {
//       resolve();
//     });
//   });

// Preload images
const preloadImages = new Promise((resolve, reject) => 
{
    imagesLoaded(document.querySelectorAll("img[class=\"img-3d\"]"), { background: true }, resolve);
});

//  let allDone = [fontOpen,fontPlayfair,preloadImages]
    let allDone = [preloadImages]

// How bad is this practice?
Promise.all(allDone).then(() => {


/**
 * Sizes
 */
 const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}


/**
 * Custom scroll 
 */ 

let scroll = new Scroll()
let currentScroll = 0
let previousScroll = 0

/**
 * Images from web
 */ 

let images = [...document.querySelectorAll('img[class=\"img-3d\"]')]
let imageStore = []
let materials = []

const addImages = () =>
{

// Base shader
let shaderMaterial = new THREE.ShaderMaterial({
    uniforms:
    {
        uTime: { value: 0 },
        uImage: { value: 0 },
        uHover: { value: new THREE.Vector2(0.5, 0.5) },
        uHoverState: { value: 0 }
    },
    side: THREE.DoubleSide,
    fragmentShader: fragment,
    vertexShader: vertex,
//  wireframe: true
})

const textureLoader = new THREE.TextureLoader()
textureLoader.setCrossOrigin('anonymous')

// Checking all images on page and creating corresponding meshes
imageStore = images.map(img => 
{
    let bounds = img.getBoundingClientRect()

    let geometry = new THREE.PlaneBufferGeometry(bounds.width, bounds.height, 100, 100)

   // let texture = textureLoader.load(img)
    // let texture = new THREE.Texture(img)
    // texture.crossOrigin = 'anonymous'

    let texture = textureLoader.load(img.src)
  //  console.log(img)
   // texture.needsUpdate = true

    let material = shaderMaterial.clone()

    // Hover events
    img.addEventListener('mouseenter',() => 
        {
            gsap.to(material.uniforms.uHoverState,
            {
                duration: 1,
                value: 1
            })
        })

    img.addEventListener('mouseout',() => 
        {
        gsap.to(material.uniforms.uHoverState,
            {
            duration: 1,
            value: 0
            })
        })

    materials.push(material)

    material.uniforms.uImage.value = texture

    let mesh = new THREE.Mesh(geometry, material)

    scene.add(mesh)

    return {
        img: img,
        mesh: mesh,
        top: bounds.top,
        left: bounds.left,
        width: bounds.width,
        height: bounds.height
    }
})
}
addImages()

const setPosition = () =>
{
    imageStore.forEach(o => 
    {
        o.mesh.position.y = currentScroll - o.top + sizes.height / 2 - o.height / 2
        o.mesh.position.x = o.left - sizes.width / 2 + o.width / 2

    })
}
setPosition()

const updateSize = () =>
{
    imageStore.forEach(o => 
    {
        // Remove geometry
        o.mesh.geometry.dispose()

        // Remove material
        o.mesh.material.dispose()

        // Remove texture?

        // Remove mesh
        scene.remove(o.mesh)

    })

    // Readding
    addImages()
    setPosition()
}


/**
 * Raycaster */ 
const raycaster = new THREE.Raycaster()

// Mouse
const mouse = new THREE.Vector2()

window.addEventListener('mousemove', (event) =>
{
    mouse.x = event.clientX / sizes.width * 2 - 1
    mouse.y = - (event.clientY / sizes.height) * 2 + 1

    raycaster.setFromCamera(mouse, camera)
    const intersects = raycaster.intersectObjects(scene.children)

    if(intersects.length > 0){
      // console.log(intersects[0])
        let obj = intersects[0].object
        obj.material.uniforms.uHover.value = intersects[0].uv
    }
})


/**
 * Resizes */ 
 window.addEventListener('resize', () =>
 {
     // Update sizes
     sizes.width = window.innerWidth
     sizes.height = window.innerHeight
 
     // Update positions
     updateSize()

     // Update camera
     camera.aspect = sizes.width / sizes.height
     camera.fov = 2 * Math.atan((sizes.height / 2) / 600 ) * (180 / Math.PI)
     camera.updateProjectionMatrix()
 
     // Update renderer
     renderer.setSize(sizes.width, sizes.height)
     renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
 
 })


/**
 * Camera
 */

// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 100, 2000)
camera.position.z = 600

// Projection to web
camera.fov = 2 * Math.atan((sizes.height / 2) / 600 ) * (180 / Math.PI)
camera.updateProjectionMatrix()

scene.add(camera)


/** 
 * Controls 
 * */ 
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true


/**
 * Renderer
 */

// Base renderer
const renderer = new THREE.WebGLRenderer({
    antialias: true,
    alpha: true,
    canvas: canvas
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))


// Composer
const composer = new EffectComposer(renderer)
const renderPass = new RenderPass(scene, camera)
composer.addPass(renderPass)

// Custom shader pass
var counter = 0.0;
let myEffect = {
    uniforms: {
      "tDiffuse": { value: null },
      "scrollSpeed": { value: null },
    },
    vertexShader: `
    varying vec2 vUv;
    void main() {
      vUv = uv;
      gl_Position = projectionMatrix 
        * modelViewMatrix 
        * vec4( position, 1.0 );
    }
    `,
    fragmentShader: `
    uniform sampler2D tDiffuse;
    varying vec2 vUv;
    uniform float scrollSpeed;
    void main(){
        vec2 newUV = vUv;
        float area = smoothstep(0.4, 0.0, vUv.y);
        area = pow(area, 4.0);
        newUV.x -= (vUv.x - 0.5) * 0.6 * area * scrollSpeed;
        gl_FragColor = texture2D( tDiffuse, newUV);
    //   gl_FragColor = vec4(area,0.,0.,1.);
    }
    `
  }

const customPass = new ShaderPass(myEffect)
customPass.renderToScreen = true

composer.addPass(customPass)



/**
 * Animate
 */
const clock = new THREE.Clock()

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()

    // Update scroll
    scroll.render()
    previousScroll = currentScroll
    currentScroll = scroll.scrollToRender

    // Don't call 
    if( Math.round(currentScroll) !== Math.round(previousScroll) )
    {
        setPosition()
        customPass.uniforms.scrollSpeed.value = scroll.speedTarget
    }

    // Update materials
    materials.forEach(m => {
        m.uniforms.uTime.value = elapsedTime
    })

    // Update controls
    controls.update()

    // Render
    // renderer.render(scene, camera)
    composer.render()

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()

})